/* Common Styles */
.side-bar {
  position: sticky;
  padding: 0;
  margin: 0;
}

.side-bar-user {
  display: flex;
  align-items: center;
}

.side-bar-user-details {
  display: flex;
  flex-direction: row;
  color: aliceblue;
  gap: 5px;
  font-size: 12px;
  color: var(--textSoft);
}

.side-car-user-username {
  font-weight: 500;
}

.side-bar-menu-cat {
  color: var(--textSoft);
  font-weight: bold;
  margin: 10px 0px;
}

.side-bar-menu-list {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  margin: 0;
}

.side-bar-menu-item {
  display: flex;
  align-items: center;
}

.side-bar-menu-catM {
  text-align: left;
  display: none;
}

.side-bar-menu-link {
  padding: 10px;
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 5px 0px;
  border-radius: 10px;
  font-size: 12px;
  font-weight: 500;
}

.side-bar-menu-link:hover {
  color: #bad8e0;
  text-decoration: underline;
  text-underline-offset: 20px;
}

.nav a.active {
  color: #1A7D99;
  text-decoration: underline;
  text-underline-offset: 20px;
}

.side-bar-logout {
  margin: 5px 0;
  display: flex;
  align-items: center;
  cursor: pointer;
  border-radius: 10px;
  border: none;
  background: none;
  color: var(--text);
  width: 100%;
  font-size: 14px;
}

.side-bar-menu-cat {
  margin-right: 90px;
  display: none;
}

.side-bar-menu-list li {
  display: flex;
}

.side-bar-menu-img {
  width: 70px;
  height: 50px;
  margin: 20px;
}

.side-bar-menuItems {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}


