    display: flex;
  }
  
  .side-bar-menu-img {
    width: 70px;
    height: 50px;
    margin: 20px;
  }
  
  .side-bar-menuItems {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  
  
  .hamburger-menu {
    display: none;
    flex-direction: column;
    justify-content: space-around;
    width: 30px;
    height: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    z-index: 10;
  }
  
  .hamburger-menu div {
    width: 30px;
    height: 3px;
    background: var(--text);
    border-radius: 10px;
    transition: all 0.3s linear;
    position: relative;
    transform-origin: 1px;
  }
  
  .hamburger-menu.open div:nth-child(1) {
    transform: rotate(45deg);
  }
  
  .hamburger-menu.open div:nth-child(2) {
    opacity: 0;
  }
  
  .hamburger-menu.open div:nth-child(3) {
    transform: rotate(-45deg);
  }
  
  .left-column {
    margin-right: 35vw;
    margin-bottom: 5vh;
  }
  
  /* Mobile Styles */
  @media (max-width: 768px) {
    .side-bar {
      display: none;
  
    }
  
    .hamburger-menu,
    .hamburger-menu.open {
      display: flex;
      padding: 2px;
      width: 40px;
      height: 40px;
    }
  
    .side-bar.show {
      display: block;
      position: fixed;
      width: 100vw;
      height: 100vh;
      background-color: var(--background);
      z-index: 9;
      margin-top: -2vh;
      top: 0;
      left: 0;
    }
  
    .side-bar-menuItems {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-top: 2vh;
      background-color: white;
      width: 100vw;
    }
  
    .side-bar-menu-img {
      display: none;
    }
  
    .side-bar-menu-cat,
    .side-bar-menu-catM,
    .side-bar-user {
      display: none;
    }
  
    .side-bar-menu-link {
      font-size: 18px;
      font-weight: 500;
      margin-right: 7vw;
      width: 100%;
      height: 100%;
      margin-bottom: 5vh;
    }
  
    .side-bar-menu-list {
      flex-direction: column;
      justify-content: flex-start;
      width: 100%;
      height: 100vh;
      margin-top: 7vh;
    }
  
    .side-bar-menu-list li {
      display: block;
      background-color: white;
      width: 55vw;
      height: auto;
    }
  
    .left-column {
      margin-right: 0;
      margin-bottom: 0;
    }
  
  
    .no-scroll {
      overflow: hidden;
      height: 100vh;
      position: fixed;
      width: 100%;
    }
  
  }
  
  /* Tablet Styles */
  @media only screen and (min-width: 1447.04px) and (max-width: 2224px) {   
  
  
    .side-bar-menu-link {
      margin: 0.6vw;
