.process-completion-message {
    background-color: white;
    padding: 20px;
    margin-top: 10px;
    width: 100%;
    height: 100vh;
    font-size: 12px;
    overflow: hidden;
    margin-top: 0;
}

.process-completion-message-Check h1 {
    font-size: 40px;
    color: rgb(72, 70, 70);
    width: 50%;
    height: 50vh;
    margin-top: 2vh;
    margin-right: 20vw;

}

.process-completion-message-Check svg {
    font-size: 120px;
    margin-right:40vw;
    color: rgba(67, 124, 140, 0.7);
    margin-top: 20vh;
    border-radius: 80px;
    border: 6px solid rgba(67, 124, 140, 0.7);
}